import React, { useState, useEffect, memo } from 'react';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';

/**
 * Component hi·ªÉn th·ªã l√° b√†i Tarot v·ªõi animation
 * @param {Object} props - Component props
 * @param {Object} props.card - Th√¥ng tin l√° b√†i
 * @param {boolean} props.isRevealed - Tr·∫°ng th√°i l√° b√†i (ƒë√£ l·∫≠t hay ch∆∞a)
 * @param {Function} props.onCardClick - Callback khi click v√†o l√° b√†i
 * @param {boolean} props.isSelectable - C√≥ th·ªÉ select l√° b√†i hay kh√¥ng
 * @param {boolean} props.isSelected - L√° b√†i ƒë√£ ƒë∆∞·ª£c ch·ªçn hay ch∆∞a
 * @param {string} props.size - K√≠ch th∆∞·ªõc l√° b√†i ("small", "medium", "large")
 * @param {string} props.position - V·ªã tr√≠ l√° b√†i trong tr·∫£i b√†i
 */
const TarotCardDisplay = memo(({ 
  card = {}, 
  isRevealed = false, 
  onCardClick, 
  isSelectable = false,
  isSelected = false,
  size = 'medium',
  position = ''
}) => {
  const [isFlipped, setIsFlipped] = useState(false);
  const [isReversal, setIsReversal] = useState(false);
  const [isHovering, setIsHovering] = useState(false);

  // X√°c ƒë·ªãnh l√° b√†i ng∆∞·ª£c d·ª±a v√†o d·ªØ li·ªáu card ho·∫∑c random
  useEffect(() => {
    if (card && card.isReversed !== undefined) {
      setIsReversal(card.isReversed);
    } else {
      // 20% c∆° h·ªôi l√° b√†i s·∫Ω ng∆∞·ª£c
      setIsReversal(Math.random() < 0.2);
    }
  }, [card]);

  // Flip animation khi isRevealed thay ƒë·ªïi
  useEffect(() => {
    if (isRevealed) {
      const timer = setTimeout(() => {
        setIsFlipped(true);
      }, 300);
      return () => clearTimeout(timer);
    } else {
      setIsFlipped(false);
    }
  }, [isRevealed]);

  // X√°c ƒë·ªãnh k√≠ch th∆∞·ªõc c·ªßa l√° b√†i
  const sizeClasses = {
    small: 'w-24 h-36',
    medium: 'w-32 h-48',
    large: 'w-40 h-60',
  };

  // Handler khi hover v√†o l√° b√†i
  const handleMouseEnter = () => {
    if (isSelectable) {
      setIsHovering(true);
    }
  };

  const handleMouseLeave = () => {
    setIsHovering(false);
  };

  // Handler khi click v√†o l√° b√†i
  const handleCardClick = () => {
    if (isSelectable && onCardClick) {
      onCardClick(card);
    }
  };

  if (!card) {
    // Tr·∫£ v·ªÅ card r·ªóng n·∫øu card l√† null ho·∫∑c undefined
    return (
      <div 
        className={`absolute ${position} transition-transform duration-300`}
        style={{ transform: 'translate(-50%, -50%)' }}
      >
        <div 
          className={`bg-gray-800 rounded-lg shadow-lg overflow-hidden ${
            size === 'small' ? 'w-24 h-36' : 
            size === 'medium' ? 'w-32 h-48' : 
            'w-40 h-60'
          }`} 
        />
      </div>
    );
  }

  // Animation variants
  const cardVariants = {
    hidden: {
      rotateY: 0
    },
    visible: {
      rotateY: 180,
      transition: {
        duration: 0.5,
        ease: "easeInOut"
      }
    }
  };
  
  const cardBackVariants = {
    hidden: {
      opacity: 1
    },
    visible: {
      opacity: 0,
      transition: {
        duration: 0.25,
        delay: 0.25
      }
    }
  };
  
  const cardFrontVariants = {
    hidden: {
      opacity: 0
    },
    visible: {
      opacity: 1,
      transition: {
        duration: 0.25,
        delay: 0.25
      }
    }
  };
  
  // Default card back image
  const defaultCardBack = '/images/tarot-card-back.jpg';
  
  return (
    <div 
      className={`absolute ${position} transition-transform duration-300`}
      style={{ transform: 'translate(-50%, -50%)' }}
    >
      <motion.div
        className={`relative ${sizeClasses[size] || sizeClasses.medium} cursor-pointer perspective-500`}
        initial="hidden"
        animate={isRevealed ? "visible" : "hidden"}
        variants={cardVariants}
        onClick={handleCardClick}
        style={{ transformStyle: 'preserve-3d' }}
      >
        {/* Card Back */}
        <motion.div
          className="absolute inset-0 backface-hidden rounded-lg shadow-lg overflow-hidden"
          variants={cardBackVariants}
        >
          <img 
            src={defaultCardBack} 
            alt="Card Back" 
            className="w-full h-full object-cover object-center"
            onError={e => {
              e.target.src = 'https://placehold.co/600x900/2a1045/9370db?text=Tarot';
            }}
          />
        </motion.div>
        
        {/* Card Front */}
        <motion.div
          className="absolute inset-0 backface-hidden rounded-lg shadow-lg overflow-hidden rotateY-180"
          variants={cardFrontVariants}
        >
          <div className={`w-full h-full relative ${card.isReversed ? 'rotate-180' : ''}`}>
            <img 
              src={card.imageUrl || 'https://placehold.co/600x900/2a1045/9370db?text=Tarot'}
              alt={card.name || 'Tarot Card'} 
              className="w-full h-full object-cover object-center"
              onError={e => {
                e.target.src = 'https://placehold.co/600x900/2a1045/9370db?text=Tarot';
              }}
            />
            
            {/* Card Name */}
            <div className="absolute inset-x-0 bottom-0 bg-gradient-to-t from-black to-transparent p-2">
              <p className="text-white text-center text-xs md:text-sm font-medium truncate">
                {card.name || 'Tarot Card'}
                {card.isReversed && ' (Reversed)'}
              </p>
            </div>
          </div>
        </motion.div>
      </motion.div>
    </div>
  );
});

TarotCardDisplay.propTypes = {
  card: PropTypes.object,
  isRevealed: PropTypes.bool,
  onCardClick: PropTypes.func,
  isSelectable: PropTypes.bool,
  isSelected: PropTypes.bool,
  size: PropTypes.oneOf(['small', 'medium', 'large']),
  position: PropTypes.string
};

export default TarotCardDisplay; 