import React, { useState, useEffect, useCallback, memo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchAllCards, performReading, getInterpretation, clearSelectedCards } from '../slices/tarotSlice';
import TarotReadingForm from './TarotReadingForm';
import ReadingResult from './ReadingResult';
import TarotReadingLayout from './TarotReadingLayout';

/**
 * Component ch√≠nh k·∫øt h·ª£p form v√† k·∫øt qu·∫£ ƒë·ªçc b√†i Tarot
 */
const TarotReading = memo(() => {
  const dispatch = useDispatch();
  const { cards, selectedCards, currentReading, interpretation, loading, error } = useSelector(state => state.tarot);
  const [readingStarted, setReadingStarted] = useState(false);
  const [readingStep, setReadingStep] = useState('form'); // 'form', 'cards', 'result'
  const [revealCount, setRevealCount] = useState(0);
  const [readingData, setReadingData] = useState(null);
  
  // Fetch t·∫•t c·∫£ cards khi component mount
  useEffect(() => {
    if (!cards || cards.length === 0) {
      dispatch(fetchAllCards());
    }
  }, [dispatch, cards]);
  
  // X·ª≠ l√Ω khi b·∫Øt ƒë·∫ßu ƒë·ªçc b√†i
  const handleStartReading = useCallback((data) => {
    if (!data) return;
    
    setReadingStarted(true);
    setReadingStep('cards');
    setReadingData(data);
    setRevealCount(0);
    
    // Reset selected cards
    dispatch(clearSelectedCards());
    
    // G·ªçi API ƒë·ªÉ l·∫•y k·∫øt qu·∫£
    dispatch(performReading(data))
      .then(() => {
        // Sau khi c√≥ k·∫øt qu·∫£, b·∫Øt ƒë·∫ßu qu√° tr√¨nh l·∫≠t b√†i
        setTimeout(() => {
          if (data && data.numCards) {
            startRevealProcess(data.numCards);
          }
        }, 1500);
      });
  }, [dispatch]);
  
  // X·ª≠ l√Ω qu√° tr√¨nh l·∫≠t b√†i t·ª´ng l√° m·ªôt
  const startRevealProcess = useCallback((totalCards) => {
    if (!totalCards || totalCards <= 0) return;
    
    let currentReveal = 0;
    
    const revealInterval = setInterval(() => {
      currentReveal += 1;
      setRevealCount(currentReveal);
      
      if (currentReveal >= totalCards) {
        clearInterval(revealInterval);
        
        // ƒê·ª£i m·ªôt ch√∫t ƒë·ªÉ ng∆∞·ªùi d√πng xem t·∫•t c·∫£ c√°c l√° b√†i
        setTimeout(() => {
          // Chuy·ªÉn sang hi·ªÉn th·ªã k·∫øt qu·∫£
          setReadingStep('result');
          
          // L·∫•y gi·∫£i th√≠ch AI n·∫øu ch∆∞a c√≥
          if (!interpretation && currentReading && currentReading.cards) {
            dispatch(getInterpretation({
              cards: currentReading.cards,
              question: currentReading.question || '',
              readingType: currentReading.readingType || 'general'
            }));
          }
        }, 2000);
      }
    }, 1200); // L·∫≠t m·ªói l√° b√†i sau 1.2 gi√¢y
    
    return () => clearInterval(revealInterval);
  }, [dispatch, currentReading, interpretation]);
  
  // X·ª≠ l√Ω khi l∆∞u k·∫øt qu·∫£
  const handleSaveReading = useCallback(() => {
    // Hi·ªÉn th·ªã th√¥ng b√°o th√†nh c√¥ng
    alert('ƒê√£ l∆∞u k·∫øt qu·∫£ th√†nh c√¥ng!');
    // Trong th·ª±c t·∫ø s·∫Ω g·ªçi API ƒë·ªÉ l∆∞u
  }, []);
  
  // X·ª≠ l√Ω khi chia s·∫ª k·∫øt qu·∫£
  const handleShareReading = useCallback(() => {
    // Hi·ªÉn th·ªã modal chia s·∫ª
    alert('Ch·ª©c nƒÉng chia s·∫ª ƒëang ƒë∆∞·ª£c ph√°t tri·ªÉn!');
    // Trong th·ª±c t·∫ø s·∫Ω hi·ªÉn th·ªã modal v·ªõi c√°c options chia s·∫ª
  }, []);
  
  // X·ª≠ l√Ω khi b·∫Øt ƒë·∫ßu l·∫°i
  const handleRestart = useCallback(() => {
    setReadingStarted(false);
    setReadingStep('form');
    setRevealCount(0);
    setReadingData(null);
    dispatch(clearSelectedCards());
  }, [dispatch]);
  
  // Render d·ª±a tr√™n b∆∞·ªõc hi·ªán t·∫°i
  let content;
  
  if (readingStep === 'form') {
    content = (
      <TarotReadingForm 
        onStart={handleStartReading}
        isLoading={loading}
      />
    );
  } else if (readingStep === 'cards') {
    content = (
      <div className="space-y-6">
        <div className="bg-white/5 backdrop-blur-sm border border-purple-900/20 rounded-xl p-6 md:p-8 text-center">
          <h2 className="text-2xl font-bold text-white mb-4 tracking-vn-tight">ƒêang tr·∫£i b√†i Tarot</h2>
          
          {currentReading && currentReading.question && (
            <div className="my-4 p-4 bg-[#9370db]/10 rounded-lg">
              <p className="text-gray-300 tracking-vn-tight italic">
                "{currentReading.question}"
              </p>
            </div>
          )}
          
          {currentReading && currentReading.cards && Array.isArray(currentReading.cards) && currentReading.cards.length > 0 && (
            <TarotReadingLayout
              readingType={readingData?.readingType || 'three-card'}
              cards={currentReading.cards}
              isRevealing={true}
              revealCount={revealCount}
            />
          )}
          
          {(!currentReading || !currentReading.cards || !Array.isArray(currentReading.cards) || currentReading.cards.length === 0) && (
            <div className="animate-pulse space-y-4">
              <p className="text-gray-300 tracking-vn-tight">Xin vui l√≤ng ch·ªù trong gi√¢y l√°t</p>
              <div className="w-20 h-20 rounded-full bg-[#9370db]/30 mx-auto flex items-center justify-center">
                <div className="w-12 h-12 rounded-full bg-[#9370db] animate-ping"></div>
              </div>
            </div>
          )}
        </div>
        
        <div className="text-center">
          <button
            onClick={handleRestart}
            className="bg-white/10 backdrop-blur-sm text-white px-4 py-2 rounded-lg font-medium hover:bg-white/20 transition-colors tracking-vn-tight inline-flex items-center"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
            H·ªßy tr·∫£i b√†i
          </button>
        </div>
      </div>
    );
  } else if (readingStep === 'result') {
    content = (
      <ReadingResult 
        reading={currentReading || {}}
        interpretation={interpretation || ''}
        isLoading={loading}
        onSave={handleSaveReading}
        onShareClick={handleShareReading}
      />
    );
  }
  
  return (
    <div>
      {error && (
        <div className="bg-red-500/20 backdrop-blur-sm border border-red-500/30 rounded-lg p-4 mb-6">
          <p className="text-white tracking-vn-tight">{error}</p>
        </div>
      )}
      
      {content}
      
      {readingStarted && readingStep === 'result' && (
        <div className="mt-8 text-center">
          <button
            onClick={handleRestart}
            className="bg-white/10 backdrop-blur-sm text-white px-6 py-3 rounded-lg font-medium hover:bg-white/20 transition-colors tracking-vn-tight inline-flex items-center"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
            </svg>
            Tr·∫£i b√†i m·ªõi
          </button>
        </div>
      )}
    </div>
  );
});

export default TarotReading; 